@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #1A1A1A;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}


.app {
    min-height: 100%;
    box-sizing: border-box;
    padding: 40px 90px 60px 90px;
    max-width: 1582px;
    text-align: left;
    margin: auto;
}

.main {
    min-height: calc(100vh - 160px);
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 0.5s linear;
}

@media only screen and (max-width: 768px) {
    .app {
        padding: 40px 20px 60px 20px;
    }
}

@media only screen and (max-width: 620px) {
    .main {
        min-height: calc(100vh - 300px);
    }
}
.header {
    width: 100%;
    height: 20px;
}

@media only screen and (max-width: 620px) {
    .header {
        height: auto;
        overflow: hidden;
    }

    .header .menu {
        display: block;
        box-sizing: border-box;
        width: 100%;
        padding: 0 20px;
        margin: 10px 0  10px 0;
        text-align: center;
    }

    .header .menu__item {
        font-size: 16px;
        margin: 5px 15px;
    }
}
.logo {
    display: inline-block;
}

.logo img {
    height: 17px;
    display: inline-block;
}
.menu {
    display: inline-block;
    float: right;
    margin: 0;
}

.menu__item, .menu__link-wrapper {
    list-style: none;
    display: inline-block;
    font-size: 13px;
    margin: 0 10px;
}

.menu__link-wrapper::last-child {
    padding-right: 0;
}

.menu__item__link, .menu__link {
    text-decoration: none;
    color: black;
    border-radius: 5px;
}

.menu__link:hover, .menu__item__link:hover { 
    text-decoration: underline;
}
.footer {
    border-top: 2px solid black;
    height: 40px;
    width: 100%;
    box-sizing: border-box;
    padding-top: 20px;
}

@media only screen and (max-width: 620px) {
    .footer .menu {
        display: block;
        width: 100%;
        padding: 0;
        margin: 10px 0  30px 0;
        text-align: center;
    }

    .footer .menu__link-wrapper{
        width: 50%;
        margin: 5px 0;
    }
}
.listings-page {
    box-sizing: border-box;
    padding-top: 60px;
    min-height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
  
    -webkit-flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.listings-page.fade-enter {
    display: none;
}

.listings-page.fade-enter-active {
    
}

@media only screen and (max-width: 620px) {
    .listings-page.fade-enter-done {
        display: block;
        padding-top: 20px;
    }
}
.listing {
    width: 50%;
    min-width: 500px;
    max-width: 100%;
    box-sizing: border-box;
    padding-bottom: 60px;
}

/* animations */

.listing__image-wrapper { 
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    transition: opacity 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

.listing__title {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    transition: opacity 0.5s ease-in-out 0.1s, -webkit-transform 0.5s ease-in-out 0.1s;
    transition: opacity 0.5s ease-in-out 0.1s, transform 0.5s ease-in-out 0.1s;
    transition: opacity 0.5s ease-in-out 0.1s, transform 0.5s ease-in-out 0.1s, -webkit-transform 0.5s ease-in-out 0.1s;
}

.listing__link {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    transition: opacity 0.5s ease-in-out 0.2s, -webkit-transform 0.5s ease-in-out 0.2s;
    transition: opacity 0.5s ease-in-out 0.2s, transform 0.5s ease-in-out 0.2s;
    transition: opacity 0.5s ease-in-out 0.2s, transform 0.5s ease-in-out 0.2s, -webkit-transform 0.5s ease-in-out 0.2s;
}

.listing-entered .listing__image-wrapper, .listing-entered .listing__link, .listing-entered .listing__title {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

/* end animations */

.listing:nth-child(2n - 1) {
    padding-right: 18px;
}

.listing:nth-child(2n) {
    padding-left: 18px;
}

.listing__title {
    font-size: 24px;
    color: #1A1A1A;
    font-weight: 500;
    padding-right: 25%;
}

.listing__image-wrapper {
    position: relative;
}

.listing__tag {
    position: absolute;
    height: 15px;
    padding-left: 6px;
    background: #fff;
    color: #6C6C6C;
    font-size: 11px;
    text-align: right;
    line-height: 10px;
    -webkit-transform: translateX(-100%) rotate(-90deg);
            transform: translateX(-100%) rotate(-90deg);
    -webkit-transform-origin: right top;
            transform-origin: right top;
    border-right: 1px solid #fff;
}

.listing__thumbnail {
    width: 100%;
}

.listing__link { 
    margin: 0;
}

.listing__link .menu__item__link {
    color: #3952f7;
    font-weight: bold;
    letter-spacing: 1px;
}

.listing__link::before {
    content: '';
    display: inline-block;
    width: 30px;
    height: 2px;
    margin-right: 15px;
    vertical-align: middle;
    background-color: #3952f7;
}

@media only screen and (max-width: 1200px) {
    .listing {
        width: 100%;
        min-width: 0;
    }
    .listing:nth-child(2n) {
        padding-left: 0;
    }
    .listing:nth-child(2n - 1) {
        padding-right: 0;
    }

    .listing__image {
        width: 100%;
    }
}

@media only screen and (max-width: 620px) {
    .listings-page {
        display: block;
    }

    .listing__title {
        font-size: 18px;
        padding-right: 5%;
    }
}
.listing-detail-page {
    box-sizing: border-box;
    width: 100%;
}

.listing-detail-page.fade-enter {
    display: none;
}

.listing-detail-page.fade-enter-done {
    display: block;
}


@media only screen and (max-width: 1480px) {
    .listing-detail {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}

@media only screen and (max-width: 768px) {
    .listing-detail {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 40px;
    }
}
.listing-detail {
    box-sizing: border-box;
    padding-top: 60px;
    padding-bottom: 100px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
}

.listing-detail__image-wrapper {
    opacity: 0;
    transition: all 0.5s ease-in-out;
}

.listing-detail__title {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    transition: all 0.5s ease-in-out 0.1s;
}

.listing-detail__question {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    transition: all 0.5s ease-in-out;
}

.listing-detail-entered .listing-detail__image-wrapper, 
.listing-detail-entered .listing-detail__question,
.listing-detail-entered .listing-detail__title {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.listing-detail__wrapper {
    display: inline-block;
    width: 560px;
    margin: 0 0 0 auto;
}

.listing-detail__title {
    font-size: 64px;
    margin-top: 60px; 
}

.listing-detail__question__number {
    font-size: 32px;
    font-weight: bold;
    color: #000;
}

.listing-detail__question__text {
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 21px;
    color: #333333;
}

.listing-detail__image-wrapper {
    width: 50%;
}

.listing-detail__thumbnail {
    display: none;
}

@media only screen and (max-width: 1480px) {
    .listing-detail__wrapper, .listing-detail__image-wrapper {
        margin: auto;
        max-width: 100%;
    }

    .listing-detail__image-wrapper {
        width: auto;
    }
    
    .listing-detail__thumbnail {
        display: block;
        width: 100%;
    }
    .listing-detail__image {
        display: none;
    }
}

@media only screen and (max-width: 768px) {
    .listing-detail__title {
        font-size: 32px;
        margin-top: 30px; 
    }
    
    .listing-detail__question__number {
        font-size: 24px;
    }
    
    .listing-detail__question__text {
        font-size: 16px;
    }
}

@media only screen and (max-width: 550px) {
    .listing-detail__title {
        font-size: 24px;
        margin-top: 20px; 
    }
    
    .listing-detail__question__number {
        font-size: 20px;
    }
    
    .listing-detail__question__text {
        font-size: 14px;
    }
}
